section.banner-section {
  transition: transform, 0.05s ease;
  position: relative;
  height: 70vh;
}

div.banner-text {
  position: absolute;
  top: 20%;
  display: grid;
  gap: 2rem;
  -webkit-gap: 2rem;
  /* background-color: rgb(0, 0, 0, 0.1); */
  /* border-top-right-radius: 50%;
  border-bottom-left-radius: 50%; */
  width: max(50%);
  z-index: 1;
  padding: 2rem;
}

div.banner-text h1 {
  text-shadow: 0 0.8rem 0.9rem var(--background-color);
}

.banner-text h1:hover {
  -webkit-text-fill-color: transparent;
  -webkit-text-stroke: 0.1rem var(--text-color);
}

.banner-text p {
  color: var(--btn-color);
  text-shadow: 0 0.8rem 0.9rem var(--background-color);
}

div.img-btn-container {
  display: grid;
  gap: 1rem;
  -webkit-gap: 1rem;
  grid-template-columns: repeat(2, 10rem);
  transition: width, var(--normal-transition) ease-in-out;
  margin-top: 3rem;
}

.img-btn-container button.banner-btn {
  background-color: transparent;
  color: var(--text-color);
  border: 0.1rem solid var(--btn-color);
  padding: 0.9em;
  border-radius: 0.4rem;
  transition: letter-spacing, background-color,
    var(--slow-transition) cubic-bezier(0.445, 0.05, 0.55, 0.95);
  width: 100%;
}

button.banner-btn:hover {
  letter-spacing: 0.05rem;
  background-color: var(--btn-color);
}

div.img-container {
  height: 70vh;
  width: 100%;
}

div.img-banner img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
  object-position: calc(50% - 20px) calc(25% + 10px);
}

@media (min-width: 580px) {
  div.img-btn-container {
    grid-template-columns: repeat(2, 20rem);
  }
}
