section.tariff-section {
  position: relative;
  z-index: 1;
  background-color: #000;
}

div.tariff-container {
  display: grid;
  gap: 3rem;
  -webkit-gap: 3rem;
  grid-template-columns: repeat(auto-fit, minmax(25rem, 1fr));
  padding: 2rem;
  margin: 0 auto;
}

article.tariff-article {
  display: grid;
  grid-template-columns: 1fr;
  gap: 1rem;
  -webkit-gap: 1rem;
  background-color: var(--primary-color);
  padding: 1rem;
  border-radius: 0.5rem;
  transition: border, transform,
    var(--normal-transition) cubic-bezier(0.445, 0.05, 0.55, 0.95);
}

.tariff-article:hover {
  border: 0.1rem solid var(--btn-color);
  transform: scale(1.02);
}

.tariff-article h3.tariff-article-title {
  font-weight: var(--heavy-font-weight);
}

p.tariff-price {
  font-size: var(--subhead-font);
  font-weight: var(--normal-font-weight);
}

p.tariff-text {
  font-size: var(--small-font);
}

small.free-trial {
  color: var(--btn-color);
}

ul.tariff-artlice-list {
  margin-top: 0.5rem;
}

.tariff-artlice-list li.tariff-article-item {
  display: grid;
  grid-template-columns: 3rem 1fr;
  align-items: center;
  gap: 0.5rem;
  -webkit-gap: 0.5rem;
}

li.tariff-article-item {
  margin-top: 1rem;
}

button.tariff-btn {
  color: var(--btn-color);
  border-radius: 0.4rem;
  border: 0.1rem solid var(--btn-color);
  background-color: transparent;
  width: 100%;
  padding: 0.5rem;
  transition: background-color, color,
    var(--slow-transition) cubic-bezier(0.445, 0.05, 0.55, 0.95);
}

.tariff-btn:hover {
  background-color: var(--btn-color);
  color: var(--text-color);
}
