div.sub-container {
  display: grid;
  gap: 1rem;
  -webkit-gap: 1rem;
  grid-template-columns: repeat(auto-fit, minmax(25rem, 1fr));
  padding: 1rem;
}

div.sub-img-container {
  border: 0.1rem solid var(--btn-color);
  border-radius: 100% 50%;
  height: 100%;
}

div.sub-img {
  background: url("./../../../../assets/tv.jpg") no-repeat center;
  background-size: cover;
  height: 90%;
  width: 70%;
  border-radius: 0.4rem;
  transform: translateX(5rem) translateY(1rem);
  height: 20rem;
}

div.sub-form-container form.sub-form {
  display: grid;
  grid-template-columns: 1fr;
  gap: 1rem;
  -webkit-gap: 1rem;
  padding: 2rem;
}

div.form-input {
  display: grid;
  grid-template-columns: 1.5fr 1fr;
  gap: 0.5rem;
  -webkit-gap: 0.5rem;
}

.form-input input,
.form-input button {
  border: none;
  border-radius: 0.4rem;
  padding: 1rem;
  outline: none;
}

.form-input input {
  background-color: var(--primary-color);
  color: var(--text-color);
}

.form-input input::placeholder {
  color: var(--text-color);
}

.form-input button {
  background-color: var(--btn-color);
  color: var(--text-color);
  font-size: var(--large-font);
}

.sub-form small span {
  color: var(--btn-color);
}
