section.movie-list-section {
  background-color: #000;
  padding: 2rem;
}

div.move-list-container {
  display: grid;
  gap: 3rem;
  -webkit-gap: 3rem;
  grid-template-columns: repeat(auto-fill, minmax(25rem, 1fr));
  margin: 0 auto;
}

div.movie-details-container {
  border-radius: 0.4rem;
  padding: 1rem;
  cursor: pointer;
  transition: box-shadow, transform, var(--slow-transition) ease-in-out;
}

div.movie-details-container:hover {
  box-shadow: 0 0.1rem 0.3rem rgb(255, 255, 255, 0.1);
  transform: translateY(-1rem);
}

div.movie-details-container:hover div.movie-about {
  display: block !important;
}

div.movie-about a.movie-about-link {
  display: inline-block;
  font-size: 1.1em;
  background-color: transparent;
  border: 0.1rem solid #333;
  border-radius: 0.2rem;
  color: var(--text-color);
  padding: 0.4rem;
  margin-top: 0.8rem;
  margin-left: auto;
  transition: border,
    var(--normal-transition) cubic-bezier(0.445, 0.05, 0.55, 0.95);
}

div.movie-about a.movie-about-link:hover {
  border: 0.1rem solid var(--btn-color);
}

div.movie-img-container {
  height: 20rem;
}

div.movie-img-container img.movie-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
  border-radius: 0.2rem;
}

.movie-list-section button.movie-list-btn {
  background-color: transparent;
  border: 0.1rem solid var(--btn-color);
  border-radius: 0.4rem;
  color: var(--text-color);
  margin-top: 2rem;
  padding: 0.5rem;
}

button.movie-list-btn span {
  transition: transform, color, font-weight,
    var(--normal-transition) ease-in-out;
}

button.movie-list-btn:hover span {
  display: inline-block;
  transform: translateX(0.4rem);
  color: var(--btn-color);
  font-weight: var(--heavy-font-weight);
}
