div.showcase-img-container {
  width: 100%;
  height: 30rem;
}

.showcase-img-container img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}

div.showcase-details-container {
  display: grid;
  gap: 1rem;
  -webkit-gap: 1rem;
}

.showcase-details-container div:first-of-type {
  display: grid;
}

div.showcase,
div.showcase-user-vote {
  display: grid;
  gap: 1rem;
  -webkit-gap: 1rem;
}

div.showcase span:first-of-type {
  border: 0.1rem solid #333;
  padding: 0.4rem;
  color: #fff7;
  font-weight: var(--heavy-font-weight);
  width: fit-content;
}

.showcase-details-container div.showcase-user-vote h3:first-of-type {
  border: 0.2rem solid green;
  border-radius: 50%;
  height: 5rem;
  width: 5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.1rem;
  padding: 0.2rem;
}

/* .showcase-details-container div:nth-child(3) {
  display: grid;
} */

@media (min-width: 730px) {
  /* div.showcase{

  } */

  div.showcase-container {
    display: grid;
    grid-template-columns: 1fr 3fr;
    gap: 2rem;
    -webkit-gap: 2rem;
    /* background: url("https://image.tmdb.org/t/p/original/1syW9SNna38rSl9fnXwc9fP7POW.jpg");
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover; */
    backdrop-filter: blur(2rem);
  }

  div.showcase,
  div.showcase-user-vote {
    display: grid;
    gap: 1rem;
    -webkit-gap: 1rem;
    grid-template-columns: 5rem 7rem;
    align-items: center;
    justify-items: center;
  }

  div.showcase-user-vote h3:last-of-type span {
    display: block;
  }
}
