html {
  font-size: 62.5%;
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

*,
*::before,
*::after {
  box-sizing: inherit;
  padding: 0;
  margin: 0;
}

:root {
  /* font */
  --nav-font: clamp(1.8rem, 2vw, 2.5rem);
  --text-font: clamp(1.2rem, 2vw, 1.6rem);
  --header-font: clamp(2.3rem, 3.5vw, 3.3rem);
  --subhead-font: clamp(1.6rem, 2.5vw, 2.4rem);
  --title-font: clamp(1.3rem, 2.2vw, 2rem);

  --ex-small-font: 0.7rem;
  --small-font: 1rem;
  --medium-font: 1.5rem;
  --large-font: 2rem;
  --ex-large-font: 2.5rem;

  /* font-size */
  --light-font-weight: 400;
  --normal-font-weight: 500;
  --heavy-font-weight: 700;

  /* color */
  --background-color: #330033;
  --btn-color: orange;
  --text-color: #fff;
  --primary-color: #333;

  /* rgb */
  --dark-rgb: rgb(25, 25, 112, 0.2);
  --light-rgb: rgb(255, 255, 255, 0.1);

  /* transiton */
  --fast-transition: 0.3s;
  --normal-transition: 0.6s;
  --slow-transition: 1s;
}

section:not(:first-of-type),
aside {
  margin-top: 5rem;
  padding: 1rem;
}

section h2 {
  margin-bottom: 3rem;
}

h1 {
  font-size: var(--header-font);
}

h2 {
  font-size: var(--subhead-font);
}

p {
  font-size: var(--text-font);
}

a {
  text-decoration: none;
}

a,
button {
  font-size: var(--text-font);
  cursor: pointer;
  font-family: inherit;
}

li {
  list-style-type: none;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  background-repeat: no-repeat;
  background: url("../src/assets/background.svg");
  background-size: contain;
  background-position: center;
  color: var(--text-color);
  background-color: #000;
  background-blend-mode: soft-light;
}

/* header */
header.header-container {
  display: grid;
  grid-template-columns: 1fr 0.9fr 0.5fr;
  justify-content: space-between;
  align-items: center;
  padding: var(--ex-large-font);
  border-bottom: 0.1rem solid var(--btn-color);
}

div.logo-container {
  font-size: var(--header-font);
  color: var(--btn-color);
}

.logo-container span {
  color: var(--text-color);
}

div.filter-container {
  background-color: var(--primary-color);
  display: flex;
  position: relative;
  border-radius: 0.4rem;
  /* justify-self: center; */
}

.filter-container input.filter-input {
  background-color: transparent;
  width: 100%;
  height: 100%;
  outline: none;
  border: none;
  padding: 0.3rem;
  padding-left: 0.7rem;
  color: var(--text-color);
}

.filter-container .search-icon {
  position: absolute;
  right: 0.4rem;
  top: 0.4rem;
}

div.hamburger-menu {
  font-size: var(--subhead-font);
  cursor: pointer;
  justify-self: end;
}

nav.nav-container {
  position: absolute;
  right: 0;
  top: 0;
  width: 70%;
  height: 50rem;
  backdrop-filter: blur(2rem);
  border-radius: 0.5rem;
  box-shadow: 0 0 0.2rem 0.4rem rgb(255, 255, 255, 0.1);
  z-index: 1;
}

.nav-container ul.nav-list {
  display: flex;
  flex-direction: column;
  padding: 2rem;
}

ul.nav-list li.nav-item {
  color: var(--text-color);
  padding-top: 5rem;
}

li.nav-item a {
  color: var(--text-color);
  font-size: var(--nav-font);
  transition: color,
    var(--slow-transition) cubic-bezier(0.455, 0.03, 0.515, 0.955);
}

li.nav-item a:hover {
  color: var(--btn-color);
}

.close-menu {
  margin-top: 2rem;
  border: 0.1rem solid var(--text-color);
  border-radius: 50%;
  width: 3rem;
  height: 3rem;
  align-self: end;
  cursor: pointer;
}

.close-menu svg {
  width: 3rem;
  padding: 0.2rem;
  fill: var(--btn-color);
  transition: transform, var(--fast-transition) ease-in-out;
}

.close-menu svg:hover {
  transform: scale(0.9);
  fill: var(--text-color);
}

/* Footer */
footer {
  margin-top: 8rem;
  background-color: #111;
  padding: 1rem;
}

div.footer-container {
  margin: 0 auto;
  width: 90%;
}

div.footer-container,
div.footer-links-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(20rem, 1fr));
  gap: 2rem;
  -webkit-gap: 2rem;
}

div.footer-brand-container {
  grid-row: 1 / 3;
}

.footer-brand-container h2 {
  font-size: var(--subhead-font);
  color: var(--btn-color);
}

h2.footer-brand-name span {
  color: var(--text-color);
}

footer h3 {
  font-size: var(--medium-font);
  color: var(--btn-color);
}

li.footer-link-item a {
  font-size: var(--small-font);
  color: var(--text-color);
}

li.footer-scoial-item,
li.footer-contact-item {
  font-size: calc(var(--medium-font) - 0.1rem);
}

.footer-copyright {
  background-color: var(--primary-color);
  height: 5rem;
  text-align: center;
  padding: 2rem;
}

@media (min-width: 800px) {
  header.header-container {
    display: grid;
    grid-template-columns: 1fr 0.7fr 1.5fr;
    justify-content: space-between;
    align-items: center;
    padding: var(--ex-large-font);
    border-bottom: 0.1rem solid var(--btn-color);
  }

  /* header */
  header.header-container {
    gap: 2rem;
  }

  div.logo-container {
    flex: 1;
  }

  /* div.filter-container {
    flex: 1;
    margin-right: auto;
    background-color: var(--primary-color);
  } */

  div.hamburger-menu {
    display: none;
  }

  nav.nav-container {
    all: unset;
    flex-grow: 1.3;
  }

  .nav-container ul.nav-list {
    all: unset;
    display: flex;
    justify-content: space-between;
  }

  ul.nav-list li.nav-item {
    padding-top: unset;
  }

  li.nav-item a {
    transition: unset;
    font-size: 1.2rem;
  }

  li.nav-item a:hover {
    all: unset;
    position: relative;
  }

  li.nav-item a:hover::after {
    content: "";
    position: absolute;
    top: auto;
    right: 0;
    left: 0;
    bottom: -0.2rem;
    height: 0.1rem;
    background-color: var(--btn-color);
  }

  .close-menu {
    display: none;
  }
}
