section {
  padding: 2rem;
}

div.faq-container {
  display: grid;
  align-items: center;
  gap: 4rem;
  -webkit-gap: 4rem;
  grid-template-columns: repeat(auto-fit, minmax(30rem, 1fr));
}

div.faq-img {
  height: 20em;
  border-radius: 0.4rem;
  background: url("./../../../../assets/faq.jpg") no-repeat center;
  background-size: cover;
  background-position: center;
  opacity: 0.5;
}

article.faq-article {
  padding: 1rem;
}

ul.faq-article-list li.faq-article-item button {
  display: flex;
  justify-content: space-between;
  padding-bottom: 1rem;
  padding-bottom: 1rem;
  width: 100%;
  background: transparent;
  border: none;
  color: var(--text-color);
  position: relative;
}

li.faq-article-item {
  position: relative;
}

li.faq-article-item button::after {
  content: "";
  position: absolute;
  top: auto;
  right: 0;
  left: 0;
  bottom: 0;
  height: 0.1rem;
  background-color: var(--btn-color);
}

li.faq-article-item button span {
  color: var(--btn-color);
}

.faq-article-item button:focus-within span {
  transform: rotate(18deg);
}

.faq-article-item button:focus-within + p {
  display: block !important;
  padding: 1rem;
}

@media (min-width: 792px) {
  div.faq-container {
    grid-template-areas: "content img";
  }

  div.faq-img {
    grid-area: img;
    height: 30em;
  }

  article.faq-article {
    grid-area: content;
  }
}
